<template>
  <section class="section about">
    <div class="container">
      <div class="section__info">
        <div class="section__info-titles-container">
          <h2 class="section__title color-primary">Client Stories</h2>
          <p class="section__subtitle">Etiam rhoncus. Maecenas tempus</p>
        </div>
      </div>
      <AppLoadingSpinner v-if="loadingStatus" />
      <div class="trainer__list" v-else>
        <TrainersMiniProfile
          v-for="trainer in trainers"
          :key="trainer.id"
          :trainer="trainer"
        />
      </div>
    </div>
  </section>
</template>

<script>
import { mapGetters } from 'vuex';
import AppLoadingSpinner from '../components/Base/AppLoadingSpinner';
import TrainersMiniProfile from '../components/Trainers/TrainersMiniProfile';
export default {
  name: 'About',
  components: {
    AppLoadingSpinner,
    TrainersMiniProfile
  },
  computed: {
    ...mapGetters(['loadingStatus', 'errorStatus', 'trainers'])
  },
  async mounted() {
    if (this.trainers && this.trainers.length > 0) return;
    try {
      await this.$store.dispatch('fetchTrainers');
    } catch (e) {
      this.errorStatus = e;
    }
  }
};
</script>

<style scoped lang="scss">
.trainer {
  &__list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(11rem, 1fr));
    grid-gap: 1rem;
    grid-auto-rows: 1fr;
  }
}
</style>
